import styles from "./Profile.module.css";
import { Link, Switch, Route, useHistory } from "react-router-dom";
import { useCallback, useEffect } from "react";
import { useDispatch } from "react-redux";
import {
  getProfileInfo,
  logOutRequest,
} from "../../services/actions/profileActions";
import ProfileForm from "../../components/ProfileForm/ProfileForm";
import ProfileOrders from "../../components/ProfileOrders/ProfileOrders";
import {
  wsConnectionClose,
  wsConnectionUserOpen,
} from "../../services/actions/socketActions";

const ProfileFeed = () => {
  const dispatch = useDispatch();

  useEffect(() => {}, [dispatch]);

  useEffect(() => {
    dispatch(wsConnectionUserOpen());

    return () => {
      dispatch(wsConnectionClose());
    };
  }, [dispatch]);

  return (
    <div className={styles.feed}>
      <ProfileOrders />
    </div>
  );
};

function Profile() {
  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    dispatch(getProfileInfo());
  }, [dispatch]);

  const onLogOut = useCallback(() => {
    dispatch(logOutRequest(history));
  }, [dispatch, history]);

  return (
    <main className={styles.section}>
      <div className={styles.container}>
        <div className={styles.links}>
          <Link to="/profile" className={styles.link} children="" />
          <Link
            to="/profile/orders"
            className={styles.link}
            children=" "
          />
          <Link
            to="/login"
            className={styles.link}
            onClick={onLogOut}
            children=""
          />
          <p
            className={styles.text}
            children="        "
          />
        </div>
        <Switch>
          <Route exact path="/profile" children={<ProfileForm />} />
          <Route exact path="/profile/orders" children={<ProfileFeed />} />
        </Switch>
      </div>
    </main>
  );
}

export default Profile;
