import { initialState } from "../rootReducer";
import { ACTIONS } from "../actions/actionTypes";

const wsReducer = (state = initialState.socket, action) => {
  switch (action.type) {
    //      WS_CONNECTION_SUCCESS
    //   wsConnected   true
    case ACTIONS.WS_CONNECTION_SUCCESS:
      return {
        ...state,
        error: undefined,
        wsConnected: true,
      };

    //      WS_CONNECTION_ERROR
    //   wsConnected   false     action.payload
    case ACTIONS.WS_CONNECTION_ERROR:
      return {
        ...state,
        error: action.payload,
        wsConnected: false,
      };

    //      WS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case ACTIONS.WS_CONNECTION_CLOSED:
      return {
        ...state,
        error: undefined,
        wsConnected: false,
      };

    //      WS_GET_MESSAGE
    //  ,     
    //  messages  ,    
    case ACTIONS.WS_GET_MESSAGE:
      return {
        ...state,
        error: undefined,
        data: action.payload,
      };
    default:
      return state;
  }
};

export default wsReducer;
