import styles from "./Login.module.css";
import { useCallback } from "react";
import Loader from "../../components/Loader/Loader";
import {
  Input,
  PasswordInput,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, Redirect, useLocation } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  postLoginRequest,
  setLoginFormValue,
} from "../../services/actions/loginActions";

function Login() {
  const dispatch = useDispatch();
  const location = useLocation();

  const { email, password } = useSelector((store) => store.login.form);
  const { loginRequest, loginFailed } = useSelector((store) => store.login);
  const { isLoggedIn } = useSelector((store) => store.profile);

  const onFormChange = (e) => {
    dispatch(setLoginFormValue(e.target.name, e.target.value));
  };

  const onFormSubmit = useCallback(
    (e) => {
      e.preventDefault();
      dispatch(postLoginRequest(email, password));
    },
    [email, password, dispatch]
  );

  if (isLoggedIn) {
    return (
      <Redirect
        //   state   undefined,   .
        to={location?.state?.from || "/react-burger"}
      />
    );
  }

  return (
    <main className={styles.section}>
      <div className={styles.container}>
        {loginRequest ? (
          <Loader />
        ) : loginFailed ? (
          <h1>    </h1>
        ) : (
          <>
            <h2 className={styles.heading}></h2>
            <form className={styles.form} onSubmit={onFormSubmit}>
              <div className={styles.input}>
                <Input
                  type={"email"}
                  placeholder={"E-mail"}
                  onChange={onFormChange}
                  value={email}
                  error={false}
                  errorText={""}
                  size={"default"}
                  name={"email"}
                />
              </div>
              <div className={styles.input}>
                <PasswordInput
                  onChange={onFormChange}
                  value={password}
                  name={"password"}
                />
              </div>
              <div className={styles.button}>
                <Button type="primary" size="medium">
                  
                </Button>
              </div>
            </form>
            <div className={styles.footer}>
              <p className={styles.text}>  ?</p>
              <Link className={styles.text} to="/register">
                
              </Link>
            </div>
            <div className={styles.footer}>
              <p className={styles.text}> ?</p>
              <Link
                className={styles.text}
                to={{ pathname: "/forgot-password" }}
              >
                 
              </Link>
            </div>
          </>
        )}
      </div>
    </main>
  );
}

export default Login;
